













import Vue from "vue";
import menu2 from "./menu2";
interface Option {
  value: number;
  text: string;
}

export default Vue.extend({
  name: "waterLevelDetailPage",
  props: ["b2_id"],
  computed: {
    profile_url() {
      let param = menu2.b2[this.b2_id].detail_params as {
        profile_url: string;
      };
      let url = param.profile_url as string;
      return url;
    },
    riverList() {
      let ret = [] as Array<Option>;
      return ret;
    }
  },
  data() {
    return {
      selected: ""
    };
  }
});
